import React from 'react';
import { Package, AlertTriangle } from 'lucide-react';
import { Product } from '../../types';
import { useInventory } from '../../contexts/InventoryContext';

interface ProductCardProps {
  product: Product;
}

export function ProductCard({ product }: ProductCardProps) {
  const { inventory } = useInventory();
  const inventoryItem = inventory.find(item => item.id === product.id);
  const stockStatus = inventoryItem?.quantity ?? 0;
  const isOutOfStock = stockStatus === 0;
  const isLowStock = stockStatus > 0 && stockStatus < 5;

  return (
    <div className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-shadow">
      <div className="relative">
        <img 
          src={product.image} 
          alt={product.name}
          className="w-full h-48 object-cover"
        />
        {/* Badge de stock mobile */}
        <div className={`absolute top-2 right-2 px-3 py-1.5 rounded-full text-sm font-bold 
          ${isOutOfStock 
            ? 'bg-red-500 text-white' 
            : isLowStock
              ? 'bg-yellow-500 text-white'
              : 'bg-green-500 text-white'
          } md:hidden`}
        >
          {isOutOfStock 
            ? '⚠️ Rupture' 
            : `${stockStatus} en stock`}
        </div>
        {/* Badge de stock desktop */}
        <div className={`absolute top-0 right-0 left-0 p-2 text-sm font-bold text-center hidden md:block
          ${isOutOfStock 
            ? 'bg-red-500 text-white' 
            : isLowStock
              ? 'bg-yellow-500 text-white'
              : 'bg-green-500 text-white'
          }`}
        >
          {isOutOfStock 
            ? '⚠️ RUPTURE DE STOCK' 
            : `${stockStatus} EN STOCK`}
        </div>
      </div>
      <div className="p-4">
        <h3 className="text-lg font-semibold">{product.name}</h3>
        {product.variant && (
          <p className="text-sm text-blue-600 mb-2">
            Parfum: {product.variant}
          </p>
        )}
        <p className="text-gray-600 text-sm mb-2">{product.description}</p>
        <div className="flex justify-between items-center mt-4">
          <p className="text-blue-600 font-bold">{product.price.toFixed(2)} €</p>
          {product.size && (
            <span className="text-sm text-gray-500">
              {product.size}
            </span>
          )}
        </div>
        {/* Statut de stock en bas de carte */}
        <div className="mt-4 flex items-center gap-2">
          {isOutOfStock ? (
            <div className="flex items-center text-red-600 font-semibold">
              <AlertTriangle className="h-5 w-5 mr-1" />
              <span className="text-sm">Rupture de stock</span>
            </div>
          ) : (
            <div className="flex items-center text-green-600 font-semibold">
              <Package className="h-5 w-5 mr-1" />
              <span className="text-sm">
                {isLowStock ? 'Stock limité' : 'Disponible'}
              </span>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}